<template>
  <div class="wsy_box">
    <!-- 商品table区域 -->
    <el-card class="box-card">
      <div class="el-card_body">
        <!-- 顶部搜索区域 -->
        <div class="wsy_top_search">
          <div class="top_search_left">
            <el-input
              placeholder="请输入内容"
              v-model="getGoodsListParams.keyword"
              @clear="clear()"
              clearable
            ></el-input>
            <el-button icon="el-icon-search" @click="findGood()"></el-button>
          </div>
          <div class="top_search_right">
            <el-button type="primary" @click='addGoods()'>添加商品</el-button>
          </div>
        </div>
        <!-- 表格区域 -->
        <el-table :data="goodsTable" stripe border style="width: 100%">
          <el-table-column type="index" label="#"></el-table-column>
          <el-table-column prop="goods_name" label="商品名称" width="600"></el-table-column>
          <el-table-column prop="goods_price" label="商品价格(元)"></el-table-column>
          <el-table-column prop="goods_number" label="商品重量"></el-table-column>
          <el-table-column label="创建时间">
            <template slot-scope="scope">{{ scope.row.add_time | timestampToTime }}</template>
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <div>
                <el-tooltip :enterable="false" content="编辑" placement="top">
                  <el-button
                    size="small"
                    type="primary"
                    icon="el-icon-edit"
                  ></el-button>
                </el-tooltip>
                <el-tooltip :enterable="false" content="删除" placement="top">
                  <el-button
                    size="small"
                    type="danger"
                    icon="el-icon-delete"
                    @click="removeUserMotal(scope.row.goods_id)"
                  ></el-button>
                </el-tooltip>
              </div>
            </template>
          </el-table-column>
        </el-table>

        <el-pagination
        background
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="getGoodsListParams.pageNum"
          :page-sizes="[1,2,5,10]"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalNum"
        ></el-pagination>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return {
      // 获取列表数据参数
      getGoodsListParams: {
        keyword: "",
        pagenum: 1,
        pagesize: 10
      },
      totalNum: 0,
      goodsTable: [],
      //   要删除的商品ID
      removeGoodID: 0,
      formLabelWidth: "120px",
    };
  },
  created() {},
  mounted() {
    this.getGoodsList();
  },
  activated() {},
  update() {},
  beforeRouteUpdate(to, from, next) {
    next();
  },
  methods: {
    // 获取商品数据
    async getGoodsList() {
      let { data } = await this.$axios.get(
        `/goods?query=${this.getGoodsListParams.keyword}&pagenum=${this.getGoodsListParams.pagenum}&pagesize=${this.getGoodsListParams.pagesize}`
      );
      console.log(data.data);

      this.totalNum = data.data.total;
      this.getGoodsListParams.pagenum = data.data.pagenum;
      this.goodsTable = data.data.goods;
    },
    handleSizeChange(val) {
      this.getGoodsListParams.pagesize = val;
      this.getGoodsList();
      // console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.getGoodsListParams.pagenum = val;
      this.getGoodsList();
      // console.log(`当前页: ${val}`);
    },
    // 添加商品跳转到添加商品页
    addGoods(){
      this.$router.push('/home/add')
    },
    // 点击删除，删除商品模态框显示
    removeUserMotal(removeGoodID) {
      this.removeGoodID = removeGoodID;
      this.$confirm("此操作将永久删除该商品, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$axios.delete(`/goods/${removeGoodID}`).then(({ data }) => {
            if (data.meta.status == 200) {
              this.getGoodsList();
              this.$message({
                type: "success",
                message: data.meta.msg
              });
            } else {
              this.$message({
                type: "error",
                message: data.meta.msg
              });
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
      this.getGoodsList();
    },
    // 根据商品ID查询商品信息
    findGood() {
      this.getGoodsList();
    },
    // 点击输入框中的清空，重新调用获取数据方法
    clear() {
      this.getGoodsList();
    }
  },
  filters: {
    // 时间戳过滤器
    timestampToTime(val) {
      let date = new Date(val * 1000); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
      let Y = date.getFullYear() + "-";
      let M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      let D =
        (date.getDate() < 10 ? "0" + date.getDate() : date.getDate()) + " ";
      let h =
        (date.getHours() < 10 ? "0" + date.getHours() : date.getHours()) + ":";
      let m =
        (date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes()) +
        ":";
      let s =
        date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
      return Y + M + D + h + m + s;
    }
  },
  computed: {
  },
  watch: {}
};
</script>

<style lang='scss' scoped>
.box-card {
  margin-top: 15px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15) !important;
  border: 1px solid #ebeef5;
  background-color: #fff;
  color: #303133;
  transition: 0.3s;
  border-radius: 4px;
  overflow: hidden;
  > .el-card_body {
    padding: 20px;
  }
}
.wsy_top_search {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  > .top_search_left {
    display: flex;
    width: 40%;
    margin-right: 20px;
  }
}
.wsy_table {
  font-size: 12px;
  margin-top: 20px;
  width: 100%;
  border: 1px #efefef;
  color: #606266;
  tr {
    > td {
      box-sizing: border-box;
      line-height: 40px;
      padding-left: 10px;
    }
  }
}
.el-pagination {
  margin-top: 20px;
}
.el-form-item__label {
  text-align: left;
}
.settingUser > p {
  padding-left: 20px;
}
.el-table {
  margin-top: 20px;
  font-size: 12px;
}
</style>
